*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

input[type=number] {
  -moz-appearance: textfield;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button,
input {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

img {
  display: flex;
  object-fit: cover;
}

* {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  background-color: #fbfbfb;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  overflow: hidden;
}

.container {
  max-width: 89.6875rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}

.title-wrapper {
  position: relative;
  text-align: center;
}
.title-wrapper--top-line {
  padding-top: 6.25rem;
}
.title-wrapper--top-line::before {
  position: absolute;
  top: 0;
  right: 50%;
  left: 50%;
  width: 3.75rem;
  height: 6.25rem;
  content: "";
  background-color: #84ff70;
  transform: translateX(-50%);
}
.title-wrapper--center-line {
  padding-top: 6.0625rem;
  margin-bottom: 3.1875rem;
}
.title-wrapper--center-line::before {
  position: absolute;
  top: 0;
  right: 50%;
  left: 50%;
  width: 3.75rem;
  height: 20rem;
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='60' height='320' viewBox='0 0 60 320' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='60' height='100' fill='%2384FF70'/%3e%3crect y='220' width='60' height='100' fill='%2384FF70'/%3e%3crect y='142' width='60' height='30' fill='%23169F00'/%3e%3crect y='190' width='60' height='30' fill='%23169F00'/%3e%3c/svg%3e ");
  transform: translateX(-50%);
}
.title-wrapper__main {
  font-size: 48px;
  font-weight: 400;
  line-height: 48px;
  text-transform: uppercase;
}
.title-wrapper__text {
  margin-top: 1.875rem;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.btn {
  display: inline-block;
  min-width: 20.125rem;
  padding: 0.75rem;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 1rem;
  transition: all 0.4s ease-in-out;
}
.btn .btn__text {
  font-size: 25px;
  font-weight: 400;
  line-height: 29px;
}
.btn--show-more {
  border: 0.0625rem solid #f95f55;
}
.btn--show-more .btn__text {
  color: #f95f55;
  transition: all 0.4s ease-in-out;
}
.btn--show-more:hover {
  background-color: #f95f55;
}
.btn--show-more:hover .btn__text {
  color: #fbfbfb;
}
.btn--buy {
  position: relative;
}
.btn--buy .btn__text {
  position: relative;
  z-index: 1;
  color: #fbfbfb;
}
.btn--buy::before, .btn--buy::after {
  position: absolute;
  top: 50%;
  right: 50%;
  bottom: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  content: "";
  border-radius: 16px;
  transition: all 0.4s ease-in-out;
  transform: translate(-50%, -50%);
}
.btn--buy::after {
  background: #fa6926;
  opacity: 1;
}
.btn--buy::before {
  background: linear-gradient(270deg, #6d4fbf 0%, #52417e 100%);
  opacity: 0;
}
.btn--buy:hover::before {
  opacity: 1;
}
.btn--buy:hover::after {
  opacity: 0;
}
.btn--discount {
  background: #f95f55;
  transition: all 0.4s ease-in-out;
}
.btn--discount .btn__text {
  color: #fbfbfb;
}
.btn--discount:hover {
  background-color: #6d4fbf;
}

.logo {
  display: flex;
  text-transform: uppercase;
}
.logo__photo {
  width: 3.8125rem;
  height: 3.1875rem;
}
.logo__content {
  margin-left: 1.4375rem;
  font-size: 16px;
  line-height: 19px;
  white-space: nowrap;
}
.logo__content-top {
  font-weight: 400;
}
.logo__content-bottom {
  font-weight: 700;
}
.logo__content span {
  display: block;
}
.logo__content span + span {
  margin-top: 0.4375rem;
}

.social {
  margin-left: 1.6875rem;
}
.social__items {
  display: flex;
}
.social__item {
  width: 2rem;
  height: 2rem;
}
.social__item + .social__item {
  margin-left: 0.9375rem;
}

.header {
  background-color: #fbfbfb;
}

.header-top {
  border-bottom: 1px solid #c4c4c4;
}
.header-top__menu {
  max-width: 58.75rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}

.top-menu__list {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.3125rem;
  text-transform: uppercase;
}
.top-menu__list-item {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}
.top-menu__list-item + .top-menu__list-item {
  margin-left: 4.375rem;
}
.top-menu__list-item-link {
  transition: all 0.4s ease-in-out;
}
.top-menu__list-item-link:hover {
  color: gray;
}

.header-center {
  display: flex;
  align-items: center;
  max-width: 106.75rem;
  height: 7.0625rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}
.header-center__lang {
  margin-right: 2.5625rem;
  margin-left: 6.0625rem;
}

.header-search {
  margin-left: auto;
}
.header-search__button {
  width: 2rem;
  height: 2rem;
  margin-left: auto;
  cursor: pointer;
  background: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M24.0889 22.2056L18.4027 16.8577C20.2569 15.0556 21.4313 12.6142 21.4313 9.88208C21.4313 4.41787 16.7339 0 10.9241 0C5.1143 0 0.416992 4.41787 0.416992 9.88208C0.416992 15.3463 5.1143 19.7642 10.9241 19.7642C13.1492 19.7642 15.2506 19.0666 16.9812 17.9621L22.791 23.4263C22.9764 23.6007 23.2236 23.6589 23.4709 23.6589C23.7181 23.6589 23.9653 23.6007 24.1507 23.4263C24.4598 23.0776 24.4598 22.5544 24.0889 22.2056ZM2.27119 9.88208C2.27119 5.40608 6.16501 1.7439 10.9241 1.7439C15.6832 1.7439 19.5771 5.40608 19.5771 9.88208C19.5771 14.3581 15.6832 18.0203 10.9241 18.0203C6.16501 18.0203 2.27119 14.3581 2.27119 9.88208Z' fill='%232F3035'/%3e%3c/svg%3e ") no-repeat center center;
  opacity: 30%;
}
.header-search__input {
  width: 0%;
  border-bottom: 1px solid #c4c4c4;
  transition: all 0.4s ease-in-out;
}
.header-search__form {
  display: flex;
}
.header-search__form--active .header-search__input {
  width: 85%;
}

.lang__items {
  display: flex;
}
.lang__item {
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  cursor: pointer;
  opacity: 0.2;
  transition: all 0.4s ease-in-out;
}
.lang__item + .lang__item {
  margin-left: 1.5rem;
}
.lang__item--active {
  opacity: 1;
}
.lang__item:hover {
  opacity: 0.6;
}

.header-bottom__menu {
  background-color: #00c1b6;
}

.menu {
  max-width: 68.25rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}
.menu__list {
  display: flex;
  align-items: center;
  height: 66px;
}
.menu__list-item {
  position: relative;
  padding: 0 1.25rem;
  color: #fbfbfb;
  text-align: center;
  text-transform: uppercase;
}
.menu__list-item::before {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 1px;
  height: 50px;
  content: "";
  background-color: #fbfbfb;
  transform: translateY(-50%);
}
.menu__list-item:last-child::before {
  display: none;
}
.menu__list-item-link {
  transition: all 0.4s ease-in-out;
}
.menu__list-item-link:hover {
  color: #d5d5d5;
}

.catalog-dropdown {
  position: absolute;
  top: -100%;
  right: 0;
  left: 0;
  background-color: #fbfbfb;
  opacity: 0;
  transition: all 0.6s ease-in-out;
}
.catalog-dropdown__items {
  column-count: 4;
  max-width: 72.5rem;
  padding: 4.375rem 0;
  margin: 0 auto;
}
.catalog-dropdown--active {
  top: 217px;
  opacity: 1;
}

.catalog-dropdown__item {
  max-width: 215px;
  margin-bottom: 28px;
}
.catalog-dropdown__item-title {
  font-size: 20px;
  line-height: 20px;
  font-family: "Bebas_Neue_Cyrillic", sans-serif;
  font-weight: 400;
}
.catalog-dropdown__item-point {
  margin-top: 6px;
}
.catalog-dropdown__item-point + .catalog-dropdown__item-point {
  margin-top: 12px;
}
.catalog-dropdown__item-link {
  font-size: 14px;
  line-height: 16px;
  color: #36414d;
}

